<template>
  <div class = "Customers">
    <b-container fluid class="card estimateContainer" id="titleConatainer">
      <b-row class="my-1">
        <label id="titleLable" class="label">Add Customer</label>
      </b-row>
    </b-container>

    <b-container fluid class="card estimateContainer" id="eDetails">

       <b-form inline @submit="onSubmit" @reset="onReset" v-if="show"> 

      <b-row class="my-1">
        <b-col sm="3">
          <label class="label">
            Name:
            <span class="text-danger">*</span>
          </label>
          <b-form-input class="rowInput " v-model="form.customer_name" size="md" id="ename" type="text" required placeholder="Name"></b-form-input>
        </b-col>

        <b-col sm="3">
          <label class="label ">Credit Allowed(add toggler):</label>
          <div>

            <b-form-checkbox v-model="form.credit_allowed" name="check-button" switch size="lg">
              <!-- <b>({{ checked }})</b> -->
            </b-form-checkbox>

          </div>
        </b-col>

        <b-col sm="3">
          <label class="label">
            Customer Type
            <span class="text-danger">*</span>
          </label>
          <!-- <b-form-input
            class="rowInput"
            size="md"
            id="enumber"
            type="text"
            placeholder="Subheading.."
          ></b-form-input>-->
          <b-form-select v-model="form.customer_type" :options="CustomerTypeList"></b-form-select>
          <div class="mt-3">
            <strong>{{ selected }}</strong>
          </div>
        </b-col>

        <b-col sm="3">
          <label class="label">
            Customer Category
            <span class="text-danger">*</span>
          </label>

          <b-form-select v-model="form.customer_category" :options="CustomerCategoryList"></b-form-select>
          <div class="mt-3">
            <strong>{{ selected2 }}</strong>
          </div>
        </b-col>
      </b-row>

      <b-row class="my-1">
        <b-col sm="3">
          <label class="label">Credit Days:</label>
          <b-form-input class="rowInput" v-model="form.credit_days" size="md" id="ename" type="text" placeholder="Contact.."></b-form-input>
        </b-col>
      </b-row>

      <b-button class="row m-3" type="submit" variant="warning">Submit</b-button>
      <b-button class="row m-2" type="reset" variant="dark">Reset</b-button>

       </b-form>
    </b-container>

    <b-container fluid class="card estimateContainer" id="titleConatainer">
      <b-row class="my-1">
        <label id="titleLable" class="label">Contacts</label>
      </b-row>
    </b-container>

    <b-container fluid class="card estimateContainer margBot" id="titleConatainer">
      <b-row class="my-1">
        <label class="label">List of Contacts with add/edit delete</label>
      </b-row>
      <b-row class="my-1">
        <input
          type="button"
          class="btn btn-default btn-warning buttonDefSize btn-raised"
          id="btnsubmit"
          value="Add Contact"
        />
      </b-row>
    </b-container>
    

        <b-card class="mt-3" header="Form Data Result">
      <pre class="m-0">{{ form }}</pre>
    </b-card>

    <!-- new added -->
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      checked: false, // for crdit allowed checkbox

      selected: null, // For cutomer type list
      CustomerTypeList: [
        { value: 1, text: "Type 1" },
        { value: 2, text: "Type 2" },
        { value: 3, text: "Type 3" }
      ],

      selected2: null, // for customer category list
      CustomerCategoryList: [
        { value: 1, text: "Cat Type 1" },
        { value: 2, text: "Cat Type 2" },
        { value: 3, text: "Cat Type 3" }
      ],

      form: {
        customer_name: "",
        credit_allowed: false,
        customer_type: "",
        customer_category: "",
        credit_days: "",
      },
      show: true,
    };
  },

  methods: {
    onSubmit(evt) {
      evt.preventDefault();
      alert(JSON.stringify(this.form));
      axios.post('http://35.225.127.81/quotation/customers/customers/?user=1',this.form)
      .then(console.log(Response))
    },
    onReset(evt) {
      evt.preventDefault();
      // Reset our form values
      this.form.customer_name = "",
        this.form.credit_allowed = false,
        this.form.customer_type =  "",
        this.form.customer_category =  "",
        this.form.credit_days =  "",
      // Trick to reset/clear native browser form validation state
      this.show = false;
      this.$nextTick(() => {
        this.show = true;
      });
    }
  }
};
</script>

<style>
@import url(https://fonts.googleapis.com/css?family=Noto+Sans:400, 700);
@import url(
  https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,
  600,
  700,
  300
);
@import "http://fonts.googleapis.com/css?family=Open+Sans:300,400,700";
.margBot {
  margin-bottom: 20px;
}
#titleLable {
  margin-bottom: 0;
  font-size: 25px;
  padding: 0px 17px;
}
.label {
  text-align: left;
  display: block !important;
  color: #666;
  font: 13px/1.55 "Open Sans", Helvetica, Arial, sans-serif;
  /* font-family: 'Noto Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif; */
  font-weight: 700;
  font-size: 100%;
}
.docIcon {
  position: absolute;
  height: auto;
  width: 33px;
  right: 5px;
  color: #9e9e9e;
}
.docIcon_1 {
  right: 38px;
}
.docIcon_2 {
  right: 71px;
}
.btn-dark, .btn-warning{
  width:7% !important;
}
.inputContainer {
  display: block;
  width: 100%;
  position: relative;
}
.text-danger {
  color: #ff6565;
}
.row {
  padding: 10px;
  width: 100%
}
.rowInput {
  height: 34px;
  border: 1px solid lightgrey !important;
  border-radius: 4px;
  padding-right: 46px;
  font: 15px/23px "Open Sans", Helvetica, Arial, sans-serif;
  color: #404040;
}
.rowInput:focus {
  border-color: #ffc000 !important;
  box-shadow: 0px 0px 3px 1px #ffc000;
}
.estimateContainer {
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.05);
}
.customercard {
  height: 40px;
  background-color: white;
  padding: 5px;
  margin-bottom: 10px;
  font-family: Helvetica;
  box-shadow: 0px 3px 8px 0px rgba(0, 0, 0, 0.5);
}

.Customers{
  margin-top: 20px;
  margin-left: 59px;
}
/* card css */
</style>


