<template>
   
    <div class="container">
        <div class="row">
			<div class="col-md-5 mx-auto">
			<div id="first">
				<div class="myform form ">
					 <div class="logo mb-3">
						 <div class="col-md-12 text-center">
							<h1>Login</h1>
						 </div>
					</div>
                   <form action=""  @submit.prevent="onSubmit">
                           <div class="form-group">
                              <input type="text" name="username"  class="form-control" id="username"  placeholder="Enter username" v-model="username">
                           </div>
                           <div class="form-group">
                              
                              <input type="password" name="password" id="password"  class="form-control"  placeholder="Enter Password" v-model="password">
                           </div>
                     
                           <div class="col-md-12 text-center ">
                              <button type="submit" class=" btn btn-block mybtn btn-primary tx-tfm">Login</button>
                           </div>
                            <div class="col-md-12 ">
                              <div class="login-or">
                                 <hr class="hr-or">
                                 <span class="span-or">or</span>
                              </div>
                           </div>
                           <!-- <div class="col-md-12 mb-3">
                              <p class="text-center">
                                 <a href="javascript:void();" class="google btn mybtn"><i class="fa fa-google-plus">
                                 </i> Signup using Google
                                 </a>
                              </p>
                           </div> -->
                           <div class="form-group">
                              <p class="text-center">Don't have account? <router-link to='/signup'>Sign up here</router-link></p>
                           </div> 
                        </form>
                 
				</div>
			</div>
			    
			</div>
		</div>
      </div>   
</template>
<script>
import axios from 'axios'
export default {
    data (){
        return{
           username: '',
           password: '',
           user: null,
           companyId:''
    }},
    computed:{
       getcompanyid(){
          let companies= this.$store.getters.sendCompanies
          return companies[0].company_id
       },
       auth(){
          return this.$store.getters.isAuthenticated
       }
    },
    methods:{
        toggle(){
            this.showSignUp = !this.showSignUp
        },
        onSubmit () {
        const formData = {
          username: this.username,
          password: this.password,
        }
        console.log(formData)
        this.$store.dispatch('login',{username:formData.username,password:formData.password})
        
      //   if(this.auth){
           
        this.$router.push('/home')
      //   }
      }
    }
}
</script>
<style >
        
        a{
         text-decoration:none !important;
         }
         h1,h2,h3{
         font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
         }
          .myform{
		position: relative;
		display: -ms-flexbox;
		display: flex;
		padding: 1rem;
		-ms-flex-direction: column;
		flex-direction: column;
		width: 100%;
		pointer-events: auto;
		background-color: #fff;
		background-clip: padding-box;
		border: 1px solid rgba(0,0,0,.2);
		border-radius: 1.1rem;
		outline: 0;
		max-width: 500px;
		 }
         .tx-tfm{
         text-transform:uppercase;
         }
         .mybtn{
         border-radius:50px;
         }
        
         .login-or {
         position: relative;
         color: #aaa;
         margin-top: 10px;
         margin-bottom: 10px;
         padding-top: 10px;
         padding-bottom: 10px;
         }
         .span-or {
         display: block;
         position: absolute;
         left: 50%;
         top: -2px;
         margin-left: -25px;
         background-color: #fff;
         width: 50px;
         text-align: center;
         }
         .hr-or {
         height: 1px;
         margin-top: 0px !important;
         margin-bottom: 0px !important;
         }
         .google {
         color:#666;
         width:100%;
         height:40px;
         text-align:center;
         outline:none;
         border: 1px solid lightgrey;
         }
          form .error {
         color: #ff0000;
         }
         /* #second{display:none;} */
</style>


